<template>
  <div>
    <div :style="{'width':gameLevel.x*24+'px'}">
      <div v-if="state==='finish'&&isWin" style="background-color:#000;color:red;text-align:Center;padding:2px 5px">
        胜利！！ <el-button style="float:right;padding:3px" size="mini">提交成绩</el-button>
      </div>
      <div v-if="state==='finish'&&!isWin" style="background-color:#000;text-align:Center;color:red">
        GAME OVER
      </div>
      <div id="game-control" class="bg-gray" >
          <el-button-group id="group-level">
            <el-button class="btn-level" size="mini" @click="init(1)">简单</el-button>
            <el-button class="btn-level" size="mini" @click="init(2)">中等</el-button>
            <el-button class="btn-level" size="mini" @click="init(3)">困难</el-button>
          </el-button-group>
          <div id="scorecard">{{timerFormat}}</div>
      </div>
      <div style="height:24px;" v-for="(row,y) in gameData" :key="'row'+y">
        <mine-cell v-for="(cell,x) in row"
          :key="'cell'+y+'-'+x" :size="24"
          :round="cell.round" :is-mine="cell.mine"  
          :is-finish="state==='finish'" 
          :is-init="state==='init'"
          :expand="cell.expand"
          @boom="finish" @open="open(y,x)"></mine-cell>
      </div>
    </div>
    
  </div>
</template>

<script>
import MineCell from "./MineCell.vue";
export default {
  name: "MineSweeper",
  data() {
    return {
      gameData: [],
      gameLevel: 0,
      state: "init", //init start 
      isWin:false,
      level: {
        1: { x: 9, y: 9, mines: 10 },
        2: { x: 16, y: 16, mines: 40 },
        3: { x: 32, y: 16, mines: 99 }
      },
      restCell: 0,
      timer:0,
      timerInterval:null//保存setInterval的返回值
    };
  },
  components: {
    MineCell: MineCell
  },
  computed:{
    timerFormat(){
      var sec = this.timer%60;
      sec = sec>9?sec:'0'+sec;
      return Math.floor(this.timer/60)+':'+sec
    }
  },
  methods: {
    timeReset(){
      clearInterval(this.timerInterval);
      this.timer = 0;
    },
    timeStart(){//计时开始
      this.timerInterval = setInterval(()=>{this.timer++},1000)
    },
    timeStop(){//计时停止
      clearInterval(this.timerInterval);
    },
    finish(win) {
      this.state = "finish";
      this.timeStop();
      if (win){
        this.isWin = true;
      } 
      else{
        this.isWin = false;
        console.log("game over");
      }
      
    },
    //1 简单 2 中等 3 困难
    init(level) {
      this.gameLevel = this.level[level];
      this.gameData.splice(0, this.gameData.length); //清空数组
      for (let i = 0; i < this.gameLevel.y; i++) {
        this.gameData[i] = []; //先把每行设置为一个数组 防止后续赋值undefined
        for (let j = 0; j < this.gameLevel.x; j++) {
          this.gameData[i][j] = {
            //初始化地雷格子
            mine: false,
            round: 0,
            expand: false
          };
        }
      }
      this.state = "init";
      this.timeReset();
    },
    start(y0, x0) {
      //start时随机创建地雷，并且第一个点开的位置不会有地雷
      for (let index = 0; index < this.gameLevel.mines; index++) {
        let x, y;
        do {
          y = Math.floor(Math.random() * this.gameLevel.y);
          x = Math.floor(Math.random() * this.gameLevel.x);
        } while ((x === x0 && y === y0) || this.gameData[y][x].mine); //当该位置是初始位置或者有地雷则重新选择
        this.gameData[y][x].mine = true;
        // console.log('y:',y," x:",x)
      }
      // 计算每个格子周围的地雷数量
      for (let i = 0; i < this.gameLevel.y; i++) {
        for (let j = 0; j < this.gameLevel.x; j++) {
          let round = 0;
          if (
            this.gameData[i - 1] &&
            this.gameData[i - 1][j - 1] &&
            this.gameData[i - 1][j - 1].mine
          )
            round++;
          if (
            this.gameData[i - 1] &&
            this.gameData[i - 1][j] &&
            this.gameData[i - 1][j].mine
          )
            round++;
          if (
            this.gameData[i - 1] &&
            this.gameData[i - 1][j + 1] &&
            this.gameData[i - 1][j + 1].mine
          )
            round++;
          if (this.gameData[i][j - 1] && this.gameData[i][j - 1].mine) round++;
          if (this.gameData[i][j + 1] && this.gameData[i][j + 1].mine) round++;
          if (
            this.gameData[i + 1] &&
            this.gameData[i + 1][j - 1] &&
            this.gameData[i + 1][j - 1].mine
          )
            round++;
          if (
            this.gameData[i + 1] &&
            this.gameData[i + 1][j] &&
            this.gameData[i + 1][j].mine
          )
            round++;
          if (
            this.gameData[i + 1] &&
            this.gameData[i + 1][j + 1] &&
            this.gameData[i + 1][j + 1].mine
          )
            round++;
          this.gameData[i][j].round = round;
        }
      }
      this.restCell =
        this.gameLevel.x * this.gameLevel.y - this.gameLevel.mines;
      this.isWin = false;
      this.state = "start";
      this.timeStart();
    },
    open(y0, x0) {
      if (this.state === "finish") return;
      if (this.state === "init") {
        this.start(y0, x0);
      }
      if (--this.restCell == 0) return this.finish(true);

      //如果当前格子四周都没有地雷，向周围8个格子拓展打开
      if (this.gameData[y0][x0].round === 0) {
        if (this.gameData[y0 - 1] && this.gameData[y0 - 1][x0 - 1])
          this.$set(this.gameData[y0 - 1][x0 - 1], "expand", true);
        if (this.gameData[y0 - 1] && this.gameData[y0 - 1][x0])
          this.$set(this.gameData[y0 - 1][x0], "expand", true);
        if (this.gameData[y0 - 1] && this.gameData[y0 - 1][x0 + 1])
          this.$set(this.gameData[y0 - 1][x0 + 1], "expand", true);
        if (this.gameData[y0][x0 - 1])
          this.$set(this.gameData[y0][x0 - 1], "expand", true);
        if (this.gameData[y0][x0 + 1])
          this.$set(this.gameData[y0][x0 + 1], "expand", true);
        if (this.gameData[y0 + 1] && this.gameData[y0 + 1][x0 - 1])
          this.$set(this.gameData[y0 + 1][x0 - 1], "expand", true);
        if (this.gameData[y0 + 1] && this.gameData[y0 + 1][x0])
          this.$set(this.gameData[y0 + 1][x0], "expand", true);
        if (this.gameData[y0 + 1] && this.gameData[y0 + 1][x0 + 1])
          this.$set(this.gameData[y0 + 1][x0 + 1], "expand", true);
        this.gameData.push(1); //由于数据比较复杂$set方法不好用，通过push+pop的方式强制手动更新视图
        this.gameData.pop();
      }
    }
  }
};
</script>

<style scoped>
  .btn-level{
    padding:7px 5px;
  }
  .desc{
    color:#454545;
    font-size: small
  }
  #game-control{
    background: #909399;
    padding:3px 10px;
  }
  #scorecard{
    background: #000;
    padding:0 5px;
    float:right;
    line-height:28px;
    font-size:20px;
    color:red
  }
</style>



