<template>
    <span 
      @click.left="open"
      @click.right.prevent="mark">
      <img :src="'/static/mine-sweeper/'+this.img+'.gif'" :height="size">
    </span>
</template>

<script>
export default {
  name: "MineCell",
  props:{
    size:Number,
    round:Number,//周围地雷数
    isMine:Boolean,//是否是地雷
    isFinish:Boolean,//是否已结束
    isInit:Boolean,
    expand:Boolean,//是否被扩展打开
  },
  data() {
    return {
      state:'init',//init 初始状态，open 打开状态, mark 标记状态,finish 结束状态
      img:'blank',
      boom:false,//是否该地雷爆炸
    };
  },
  watch: {
    isInit: function (val) {
      if(val){
        // console.log('init ... ')
        this.init();
      }
    },
    isFinish: function (val) {
      if(val){
        // console.log('watch finish')
        this.finish();
      }
    },
    expand: function (val) {
      if(val&&!this.isMine){
        this.open();
      }
    },
    state: function(newVal,oldVal){
      // console.log(newVal,oldVal);
      switch (newVal) {
        case 'init':
          return this.img= 'blank';
        case 'open':
          if(this.isMine){
            return this.img= 'mine2';
          }
          return this.img= this.round;
        case 'mark':
          return this.img= 'flag';
        case 'finish':
          if(this.isMine){
            if(oldVal==='mark')
              return this.img= 'mine1';
            if(this.boom)
              return this.img= 'mine2';
            return this.img= 'mine';
          }else{
            return this.img= this.round;
          }
      }
    }

  },
  computed:{
    // style(){
    //   return {
    //     'height':this.size+'px',
    //     'width':this.size+'px',
    //     'background': 'url(/static/mine-sweeper/'+this.img+'.gif) no-repeat 0px 0px',
        
    //   }
    // }
  },
  methods:{
    init(){
      this.state='init'
    },
    open(){
      if(this.state==='init'||this.state==='mark'){
        this.state='open'
        if(this.isMine){
          this.boom=true;
          this.$emit('boom');
        }else{
          this.$emit('open');
        }
      }
    },
    mark(){
      if(this.state==='init')
        this.state='mark'
      else if(this.state==='mark'){
        this.state='init'
      }
    },
    finish(){
      this.state='finish'
    }
  }
};
</script>

<style scoped>
</style>



