<template>
  <el-container :style="{height:containerHeight+'px'}">
    <el-header id="main-head">
      <div style="margin-left:180px;float:left;position:relative">
        <a class="a-info" href="javascript:void(0)" title="关注微信公众号,在微信上控制lzlz.site" 
          v-on:mouseover="showBarcode=true" v-on:mouseout="showBarcode=false" >
          <i class="fa fa-qrcode fa-lg" aria-hidden="true"></i>
        </a>
        <img v-show="showBarcode" src="./assets/wx.jpg" height="200" 
            style="position:absolute;left:0;top:70px;z-index:999">
      </div>
      <div style="float:right;line-height:60px;">
        <a class="a-info" href="javascript:void(0)" >
          <i class="fa fa-cog fa-lg" aria-hidden="true"></i>
        </a>&nbsp; 
        
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px" >
        <el-menu id="nav1"  @select="handleSelect"
          :default-active="nav.activeIndex">
          <el-menu-item index="my">
            <i class="fa fa-user-circle-o fa-lg fixed-width"></i>
            <span slot="title">我的</span>
          </el-menu-item>
          <el-menu-item index="3"  >
            <i class="fa fa-handshake-o fa-lg fixed-width" aria-hidden="true"></i>
            <span slot="title">邀请</span>
          </el-menu-item>
          <el-menu-item index="4">
            <i class="fa fa-hand-scissors-o fa-lg fixed-width" aria-hidden="true"></i>
            <span slot="title">发起竞猜</span>
          </el-menu-item>
        </el-menu>
        <el-menu @select="handleSelect"
            :default-active="nav.activeIndex"
            active-text-color="#67C23A">
            <h4 style="margin:5px 10px 0;text-align:center">
              <i class="fa fa-gamepad fa-lg" aria-hidden="true"></i> 游戏
            </h4>
            <el-menu-item index="mine-sweeper">
              <i class="fixed-width" aria-hidden="true"><img src="/static/mine-sweeper/icon.gif" height="18"></i>
              <span slot="title">扫雷</span>
            </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main style="border-left:2px solid #e4e7ed;padding:10px 20px">
        <keep-alive>
          <router-view v-if="keepAlive.indexOf(nav.activeIndex)>=0"></router-view>
        </keep-alive>
        <router-view v-if="keepAlive.indexOf(nav.activeIndex)<0"></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      containerHeight: window.innerHeight,
      showBarcode:false,
      keepAlive:['mine-sweeper'],
      nav:{
        activeIndex: '1',
      },
    };
  },
  methods: {
    // handleNav1Select(key, keyPath) {
    //   this.nav.activeIndex1 = key;
    //   this.handleNavSelect(key);
    //   // console.log('nav1',key, keyPath);
    // },
    // handleNav2Select(key, keyPath) {
    //   this.nav.activeIndex2 = key;
    //   this.handleNavSelect(key);
    //   // console.log('nav2',key, keyPath);
    // },
    handleSelect(key){
      this.nav.activeIndex = key;
      this.$router.push(key);
    }
  },
  mounted() {
    const that = this;
    window.onresize = () => {
      that.containerHeight = window.innerHeight;
    };
  }
};
</script>

<style scoped>
#main-head {
  line-height: 60px;
  background: #e4e7ed url(./assets/lzlzsite.png) no-repeat 20px center;
  background-size: auto 40px;
}
#nav1{
  padding-bottom:20px;
  border-bottom: 3px solid #E4E7ED
}
.panel {
  border: 2px solid #e4e7ed;
  border-bottom: none;
  background-color: white;
}
.fixed-width{
  display: inline-block;
  width:30px;
}
</style>



