import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import My from '@/components/My'
import MineSweeper from '@/components/mine-sweeper/MineSweeper'

Vue.use(Router)

export default new Router({
  routes: [ 
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '/mine-sweeper',
      name: 'MineSweeper',
      component: MineSweeper
    }
  ]
})



